@import "../../index.sass";

// Components
@import "../Hero";
@import "../Navbar";
@import "../PostListItem";

//Modals
@import "../_modals/AddPostModal";
@import "../_modals/EnterPostIdModal";
@import "../_modals/MessageModal";

#home {
    min-height: 60vh;
    height: 100%;
    margin-top: 0;
    padding-bottom: 4em;
    background-color: $grey3;
    .container {
        .section-title {
            padding-top: 1em; }
        .post-list {}
        .load-more {
            margin-top: 2em;
            display: flex;
            align-items: center;
            justify-content: center;
            .empty {
                font-size: 1.4em;
                background-color: #e2e2e2;
                padding: 1em;
                border-radius: 0.5em;
                color: $dark3; } } }

    @media screen and ( max-width: 425px ) {
        .container {
            #load-more {
                width: 100%;
                display: flex;
                align-items: center;
                justify-content: center; } } } }
