@import "../index";

.post-list-item {
    margin-top: 1em;
    border-radius: 0.25em;
    padding: 1em 2em;
    background-color: #fff;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    transition: all 0.25s ease;
    box-shadow: rgba(0, 0, 0, 0.02) 0px 1px 3px 0px, rgba(27, 31, 35, 0.15) 0px 0px 0px 1px;
    //Title
    .title {
        font-weight: 600;
        font-size: 1.2em; }
    //Buttons
    .buttons {
        display: flex;
        .button {
            margin-left: 0.5em;
            font-size: 1em;
            svg {
                font-size: 1.4em;
                margin-right: 0.5em; } } }
    &:hover {
        opacity: 0.9; }

    //Media Queries
    @media screen and ( max-width: 1024px ) {
        .buttons {
            .button {
                padding: 1em;
                svg {
                    margin-right: 0; }
                .text {
                    display: none; } } } }

    @media screen and ( max-width: 768px ) {
        flex-direction: column;
        justify-content: flex-start;
        align-items: flex-start;
        padding: 1em;
        .title {
            font-size: 1em; }
        .buttons {
            margin-top: 1em;
            width: 100%;
            justify-content: space-between;
            .button {
                width: 100%;
                align-items: center;
                justify-content: center;
                padding: 0.5em;
                &:first-child {
                    margin-left: 0; } } } }

    @media screen and ( max-width: 425px ) {

        padding: 0.75em 1em;

        .title {} } }
