//Fonts
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@200;500;700;900&display=swap');

//Colors
$dark1: #111111;
$dark2: #222222;
$dark3: #363636;
$grey1: #a8a8a8;
$grey2: #cacaca;
$grey3: #f1f6f9;

$red: #fc5c65;
$redB: #ffe4e5;

*,*::after,*::before {
    font-family: "Poppins", sans-serif;
    box-sizing: border-box; }

body {
    background-color: $dark1; }

//Global Styles

// -- Section
.section {
    width: 100%; }

// -- Container
.container {
    width: 90vw;
    max-width: 110em;
    margin: 0 auto;
    .section-title {
        margin: 0;
        font-size: 2em;
        width: 100%;
        margin-bottom: 1em;
        border-bottom: 2px solid #dddddd;
        @media screen and ( max-width: 768px ) {
            margin-bottom: 0.5em; } }
    @media screen and ( max-width: 768px ) {
        width: 85vw; } }
// -- Container Variant
.small {
    width: 80vw;
    max-width: 100em; }

//Input, TextArea
input,
textarea {
    padding: 1em 1.5em;
    outline: none;
    border: none;
    background-color: $grey3;
    border-radius: 0.25em;
    width: 100%;
    margin-top: 1em;
    font-size: 1.2em; }

//Scrollbar
::-webkit-scrollbar {
    width: 20px;
    background: $dark1; }

::-webkit-scrollbar-track {}

::-webkit-scrollbar-thumb {
    background: $dark3; }
