@import "../index";

#navbar {
    position: absolute;
    top: 1em;
    left: 0;
    z-index: 1000;
    .container {
        display: flex;
        align-items: center;
        justify-content: flex-end;
        //Buttons
        .buttons {
            display: flex;
            .button {
                margin-left: 0.5em;
                width: 13vw;
                min-width: 11em;
                justify-content: center; }

            //Dropdown Button
            .dropdown {
                position: relative;
                .drop-items {
                    position: absolute;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    flex-direction: column;
                    background-color: $dark2;
                    border-radius: 0.5em;
                    padding: 0.25em;
                    margin-top: 0.5em;
                    width: 100%;
                    box-shadow: rgba(255, 255, 255, 0.1) 0px 1px 1px 0px inset, rgba(50, 50, 93, 0.25) 0px 50px 100px -20px, rgba(0, 0, 0, 0.3) 0px 30px 60px -30px;
                    .button {
                        font-size: 1em;
                        width: 100%;
                        margin-top: 0.25em;
                        margin-right: 0;
                        justify-content: flex-start;
                        &:first-child {
                            margin-top: 0; } } } } }

        //Profile Info
        .profile-info {
            background-color: $grey3;
            width: fit-content;
            display: flex;
            align-items: center;
            justify-content: flex-start;
            padding: 0.5em 2em;
            font-size: 1.25em;
            border-radius: 0.25em;
            transition: all 0.4s ease;
            box-shadow: rgba(0, 0, 0, 0.02) 0px 1px 3px 0px, rgba(27, 31, 35, 0.15) 0px 0px 0px 1px;
            .profile-image {
                width: 2em;
                height: 2em;
                background-color: $dark1;
                border-radius: 50%;
                margin-right: 0.5em; } } }
    @media screen and ( max-width: 768px ) {
        .container {
            justify-content: flex-end;
            .buttons {
                .dropdown {
                    #main-button {
                        display: flex;
                        align-items: center;
                        justify-content: center; }

                    .drop-items {
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        .text {
                            display: none; }
                        svg {
                            margin-right: 0;
                            font-size: 20px; } } } }
            #profile-info {
                display: none; } } }

    @media screen and ( max-width: 425px ) {
        .container {
            .buttons {
                flex-direction: column;
                width: 100%;
                .button {
                    margin: 0;
                    width: 100%;
                    margin-bottom: 0.5em; }
                #main-button {
                    svg {
                        margin-right: 0.5em; }
                    .text {
                        display: flex; } } } } } }
